// LoadingDialog.js
import React from 'react';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import CircularProgress from '@mui/material/CircularProgress';
import Box from '@mui/material/Box';

const LoadingDialog = ({ open, onClose, title, message }) => {
  return (
    <Dialog
      open={open}
      onClose={onClose}
      aria-labelledby="loading-dialog-title"
      maxWidth="xs"
      fullWidth
    >
      <DialogTitle id="loading-dialog-title">{title}</DialogTitle>
      <DialogContent>
        <Box display="flex" flexDirection="column" alignItems="center" justifyContent="center" minHeight="150px">
          <CircularProgress />
          {message && <Box mt={2}>{message}</Box>}
        </Box>
      </DialogContent>
    </Dialog>
  );
};

export default LoadingDialog;
