import { useEffect, useState } from 'react';
// import { Dropbox } from 'dropbox';
import './App.css';
import '@fortawesome/fontawesome-free/css/all.min.css';
import {
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
  MenuItem,
  Select,
} from '@mui/material';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider, DatePicker } from '@mui/x-date-pickers';
import CloseIcon from '@mui/icons-material/Close';
import LoadingDialog from './othercomp/LoadingDialog';
import ConfirmationDialog from './othercomp/ConfirmationDialog';
import imageCompression from 'browser-image-compression';
import * as Realm from 'realm-web';
import { ObjectId } from 'bson';





function App() {

  const [rows, setRows] = useState([]);
  const [editIndex, setEditIndex] = useState(null);
  const [selectedImageIndex, setSelectedImageIndex] = useState(null);
  const [selectedRow, setSelectedRow] = useState(null);
  const [loading, setLoading] = useState(false);
  const [confirmationDialogOpen, setConfirmationDialogOpen] = useState(false);

  // const accessToken = 'sl.B6brLVzitAYQvG_7ERGPHKWMAY3mcVZ9WN8xw-f7gBJwgbyVuw7PKfKh_y-l99jFnX__8GMgtl9Kl2fwA0DaJ-HhhHHl-GFeOkb5XOwXIUGAS8xhjdQtklpYiGu3PVu_JYiEwNWvl9rs2d3k9N9w6Ew'; // Replace with your Dropbox access token

  const REALM_APP_ID = 'application-0-zbeziij'; // Replace with your Realm app ID
  const app = new Realm.App({ id: REALM_APP_ID });
  const atlasAPIKey = '7ewggVN9i5uypNgoRn6GGEFIFs55wE2gY0jf5DEEhr29ff4ZNbRo4Upt3BNGNcAq';
  const credentials = Realm.Credentials.apiKey(atlasAPIKey);
  const mongoDBName = 'medilifedb'
  const mongoCollection = 'medilifecl'
  let itemsCollection = null;
  let isMogoAuthenticated = false; // Track authentication state
  let user = null;

  const initMongoDB = async () => {
    if (isMogoAuthenticated && itemsCollection) {
        // Already authenticated and MongoDB is initialized
        return itemsCollection;
    }

    // Ensure user is logged in
    if (!isMogoAuthenticated) {
        user = await app.logIn(credentials);
        isMogoAuthenticated = true;
    }

    const mongo = user.mongoClient('mongodb-atlas');
    const db = mongo.db(mongoDBName);
    itemsCollection = db.collection(mongoCollection);
    
    return itemsCollection;
  };

  const getMongoItemsCollection = async () => {
      if (isMogoAuthenticated) {
          return itemsCollection;
      } else {
          await initMongoDB();
          return itemsCollection;
      }
  };


  const formatterIndCurr = new Intl.NumberFormat('en-IN', {
    style: 'currency',
    currency: 'INR',
    minimumFractionDigits: 0,
    maximumFractionDigits: 2,
  });

  useEffect(() => {
    const fetchData = async () => {
      try {        
        // Fetch data
        const collection = await getMongoItemsCollection();
        const data = await collection.find();
        
        // const response = await axios.get(`${apiBaseUrl}/api/records`);
        // setRows(response.data);
        setRows(data)
      } catch (error) {
        console.error('Error fetching data from API:', error);
      }
    };

    fetchData();
  }, []);

  const addRow = async () => {
    const today = new Date().toISOString();
    const newRow = {
      date: today,
      id: '',      
      payout: '',
      compound: '',
      balance: '',
      paymentUploadedFiles: [],
      status: 'Pending'
    };

    // axios.post(`${apiBaseUrl}/api/records`, newRow)
    //   .then(response => {
    //     setRows(prevRows => [...prevRows, response.data]);
    //     // setEditIndex(rows.length);
    //   })
    //   .catch(error => console.error('Error adding row:', error));

        const collection = await getMongoItemsCollection();
        const resultId = await collection.insertOne(newRow)
        
        // const updatedRowResult = await itemsCollection.findOneAndUpdate(
        //   { _id: resultId.insertedId },
        //   { $set: { uuid: resultId.insertedId.toString() } },
        //   { returnNewDocument: true } // Return the updated document
        // );
        // setRows(prevRows => [...prevRows, updatedRowResult]);
        // // setEditIndex(rows.length);
        
        collection.findOneAndUpdate(
          { _id: resultId.insertedId },
          { $set: { uuid: resultId.insertedId.toString() } },
          { returnNewDocument: true } // Return the updated document
        ).then(updatedRowResult => {
          setRows(prevRows => [...prevRows, updatedRowResult]);
          // setEditIndex(rows.length);
        })
        .catch(error => console.error('Error adding row:', error));

        
        console.log(rows)

  };

  const removeRow = async (index) => {
    if (rows.length > 0) {
      const rowToRemove = rows[index];
      const uuid = rowToRemove.uuid;

      // const dbx = new Dropbox({ accessToken });

      console.log(rows)
      // const fileDeletionPromises = rowToRemove.paymentUploadedFiles.map(({ path }) =>
      //   dbx.filesDeleteV2({ path })
      // );

      try {
        setLoading(true); // Show spinner
        // await Promise.all(fileDeletionPromises);
        // await axios.delete(`${apiBaseUrl}/api/records/${uuid}`);
        
        // Remove the record from MongoDB
        const collection = await getMongoItemsCollection();
        const deleteResult = await collection.deleteOne({ _id: new ObjectId(uuid) });
        console.log('deleted '+ await deleteResult.deletedCount+' record.')

        setRows(prevRows => prevRows.filter((_, i) => i !== index));
        setLoading(false); // Hide spinner
      } catch (error) {
        setLoading(false); // Hide spinner in case of error
        console.error('Error removing row or saving data:', error);
      }
    }
  };

  const handleInputChange = (index, event) => {
    const { name, value } = event.target;
    setRows(prevRows =>
      prevRows.map((row, i) => {
        if (i === index) {
          const newRow = { ...row, [name]: value };
          if (name === 'payout') {
            newRow.compound = parseFloat(roundUpToNearestTenThousand(newRow.payout)) || 0;
          }

          if (name === 'payout' || name === 'compound') {
            newRow.balance = (parseFloat(newRow.payout) || 0) - (parseFloat(newRow.compound) || 0);
          }
          return newRow;
        }
        return row;
      })
    );
  };

  const handleDateChange = (index, date) => {
    setRows(prevRows =>
      prevRows.map((row, i) =>
        i === index ? { ...row, date: date.toISOString() } : row
      )
    );
  };

  const handleEditClick = (index) => {
    setEditIndex(index);
  };

  const handleSaveClick = async (index) => {
    try {
      const row = rows[index];
      setLoading(true); // Show spinner
      // await axios.put(`${apiBaseUrl}/api/records/${row.uuid}`, row);
      const collection = await getMongoItemsCollection();
      collection.findOneAndUpdate(
        { _id: new ObjectId(row.uuid) },
        { ...row} ,
        { returnNewDocument: true } // Return the updated document
      )
      setEditIndex(null);
      setLoading(false); // Hide spinner
    } catch (error) {
      setLoading(false); // Hide spinner in case of error
      console.error('Error saving data:', error);
    }
  };

  const handleFileChange = async (id, index, event) => {
    const files = event.target.files;
    if (files.length > 0) {
      // const dbx = new Dropbox({ accessToken });
      const date = new Date()
      const timestamp = date.getTime()
      const dateSt = date.toLocaleDateString('en-GB').replaceAll('/','-')

      const uploadedFilePromises = Array.from(files).map(async file => {

        // Compress the image
        const options = {
          maxSizeMB: 1, // Maximum size in MB
          maxWidthOrHeight: 800, // Maximum width or height
          useWebWorker: true, // Use web worker for better performance
        };
        const compressedFile = await imageCompression(file, options);

        const fileExtension = 'jfif'
        // const fileExtension = file.type.replace('image/', '')
        
        // Convert file to base64
        const reader = new FileReader();
        reader.readAsDataURL(compressedFile);
        return new Promise((resolve, reject) => {
          reader.onloadend = () => {
            const base64data = reader.result;
            const fileNameWithTimestamp = `Payment_${(id && id.length>0)?id:file.name}_${dateSt}_${timestamp}.${fileExtension}`;
            resolve({ fileName: fileNameWithTimestamp, fileData: base64data });
          };
          reader.onerror = reject;
        });
      });

      try {
        setLoading(true); // Show spinner
        const fileMetadata = await Promise.all(uploadedFilePromises);
        setRows(prevRows =>
          prevRows.map((row, i) =>
            i === index ? { ...row, paymentUploadedFiles: [...row.paymentUploadedFiles, ...fileMetadata] } : row
          )
        );

        const row = rows[index];
        // await axios.put(`${apiBaseUrl}/api/records/${row.uuid}`, row);
        
        const collection = await getMongoItemsCollection();
        collection.findOneAndUpdate(
          { _id: new ObjectId(row.uuid) },
          { $set: { paymentUploadedFiles: row.paymentUploadedFiles } },
          { returnNewDocument: false } // Return the updated document
        ).then(updatedRowResult => {
          console.log(updatedRowResult.length)
          // setRows(prevRows => [...prevRows, updatedRowResult]);
          // setEditIndex(rows.length);
        })
        .catch(error => console.error('Error adding image:', error));


        setLoading(false); // Hide spinner
      } catch (error) {
        setLoading(false); // Hide spinner in case error
        console.error('Error uploading files:', error);
      }
    }
  };

  const removeImage = async (rowIndex, fileName) => {
    // const dbx = new Dropbox({ accessToken });
    try {
      setLoading(true); // Show spinner
      // await dbx.filesDeleteV2({ path: filePath });
      setRows(prevRows =>
        prevRows.map((row, i) =>
          i === rowIndex ? { ...row, paymentUploadedFiles: row.paymentUploadedFiles.filter(file => file.fileName !== fileName) } : row
        )
      );

      const row = rows[rowIndex];
      // await axios.put(`${apiBaseUrl}/api/records/${row.uuid}`, row);
      const collection = await getMongoItemsCollection();
      collection.findOneAndUpdate(
        { _id: new ObjectId(row.uuid) },
        { ...row} ,
        { returnNewDocument: false } // Return the updated document
      )
      setLoading(false); // Hide spinner
    } catch (error) {
      setLoading(false); // Hide spinner in case of error
      console.error('Error removing image:', error);
    }
  };

  const removeAll = async () => {
    // const dbx = new Dropbox({ accessToken });

    // const fileDeletionPromises = rows.flatMap(row =>
    //   row.paymentUploadedFiles.map(({ path }) => dbx.filesDeleteV2({ path }))
    // );

    // const deleteRowEntries = rows.flatMap(async row =>
    //   await axios.delete(`${apiBaseUrl}/api/records/${row.uuid}`)
    // );

    
    // const deleteRowEntries = rows.flatMap(async row =>{
    //     // Remove the record from MongoDB     aaaaaaaaaa 
    //     const collection = await getMongoItemsCollection();
    //     const deleteResult = await collection.deleteOne();
    //     console.log('deleted '+ await deleteResult.deletedCount+' record.')
    //   }
    // );

    const deleteRowEntries = async () =>{
      // Remove the record from MongoDB     aaaaaaaaaa 
      const collection = await getMongoItemsCollection();
      const deleteResult = await collection.deleteOne();
      console.log('deleted '+ await deleteResult.deletedCount+' record.')
    }
    

    try {
      setLoading(true); // Show spinner
      await Promise.all([
        // ...fileDeletionPromises,
        ...deleteRowEntries,
      ]);

      setRows([]);
      setLoading(false); // Hide spinner
    } catch (error) {
      setLoading(false); // Hide spinner in case error
      console.error('Error deleting files:', error);
    }
  };

  const handleRemoveAllClick = () => {    
    setConfirmationDialogOpen(true)
  };

  const handleConfirmRemoveAllClick = () => {
    removeAll(); // Assuming removeAll is a function that deletes all rows
    setConfirmationDialogOpen(false);
  };

  const handleImageClick = (row, index) => {
    setSelectedRow(row);
    setSelectedImageIndex(index);
  };

  const handleCloseDialog = () => {
    setSelectedRow(null);
    setSelectedImageIndex(null);
  };

  const handlePrevImage = () => {
    setSelectedImageIndex(prevIndex => (prevIndex - 1 + selectedRow.paymentUploadedFiles.length) % selectedRow.paymentUploadedFiles.length);
  };

  const handleNextImage = () => {
    setSelectedImageIndex(prevIndex => (prevIndex + 1) % selectedRow.paymentUploadedFiles.length);
  };

  const handleDownload = (linkUrl, fileName) => {
    const link = document.createElement('a');
    link.href = linkUrl;
    link.download = fileName
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  return (
    <div className="App">
      <img src="https://www.medilifecare.in/assets/img/logo.png" alt="Medilife Logo" />
      <h2>Medilife Compound Data</h2>
      <div className="controls">
        <Button variant="contained" color="primary" onClick={addRow}>Add Row</Button>
        <Button variant="contained" color="secondary" onClick={handleRemoveAllClick}>Remove All</Button>
      </div>
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Date</TableCell>
              <TableCell>ID</TableCell>
              <TableCell>Payout</TableCell>
              <TableCell>Compound</TableCell>
              <TableCell>Balance</TableCell>
              <TableCell>Payment</TableCell>
              <TableCell>Status</TableCell>
              <TableCell>Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row, rowIndex) => (
              <TableRow key={row.uuid}>
                <TableCell>
                  {editIndex === rowIndex ? (
                    <LocalizationProvider dateAdapter={AdapterDateFns}>
                      <DatePicker
                        value={new Date(row.date)}
                        onChange={(date) => handleDateChange(rowIndex, date)}
                        slotProps={{
                          textField: {
                            variant: 'standard', // Use the appropriate TextField props
                            fullWidth: true
                          }
                        }}
                      />
                    </LocalizationProvider>
                  ) : (
                    new Date(row.date).toLocaleDateString('en-GB')
                  )}
                </TableCell>
                <TableCell>
                  {editIndex === rowIndex ? (
                    <TextField
                      type="text"
                      name="id"
                      value={row.id}
                      onChange={(e) => handleInputChange(rowIndex, e)}
                      variant="outlined"
                      size="small"
                    />
                  ) : (
                    row.id
                  )}
                </TableCell>
                <TableCell>
                  {editIndex === rowIndex ? (
                    <TextField
                      type="number"
                      name="payout"
                      value={row.payout}
                      onChange={(e) => handleInputChange(rowIndex, e)}
                      variant="outlined"
                      size="small"
                    />
                  ) : (
                    formatterIndCurr.format(row.payout)
                  )}
                </TableCell>
                <TableCell>
                  {editIndex === rowIndex ? (
                    <TextField
                      type="number"
                      name="compound"
                      value={row.compound}
                      onChange={(e) => handleInputChange(rowIndex, e)}
                      variant="outlined"
                      size="small"
                    />
                  ) : (
                    formatterIndCurr.format(row.compound)
                  )}
                </TableCell>
                <TableCell className="highlight">
                  {editIndex === rowIndex ? (
                    <TextField
                      type="number"
                      name="balance"
                      value={row.balance}
                      onChange={(e) => handleInputChange(rowIndex, e)}
                      variant="outlined"
                      size="small"
                      disabled
                    />
                  ) : (
                    formatterIndCurr.format(row.balance)
                  )}
                </TableCell>
                <TableCell>
                  {editIndex === rowIndex ? (
                    <>
                      <input
                        type="file"
                        multiple
                        onChange={(e) => handleFileChange(row.id, rowIndex, e)}
                        accept="image/*"
                      />
                      {row.paymentUploadedFiles.map(({ fileData, fileName }, i) => (
                        <div key={i} style={{ marginBottom: '10px' }}>
                          {/* <Button variant="contained" color="primary" onClick={() => handleImageClick(row, i)}>View</Button> */}
                          <img
                            src={fileData}
                            alt="Uploaded"
                            style={{ width: '50px', height: '50px', marginLeft: '10px', cursor: 'pointer' }}
                            onClick={() => handleImageClick(row, i)}
                          />&nbsp;&nbsp;
                          <i className="fas fa-eye fa-lg" style={{ color: '#007bff' }} title="View Image" onClick={() => handleImageClick(row, i)}></i>&nbsp;&nbsp;
                          <i className="fas fa-trash fa-lg" style={{ color: 'red' }} title="Delete Image" onClick={() => removeImage(rowIndex, fileName)}></i>
                          {/* <Button variant="contained" color="secondary" onClick={() => removeImage(rowIndex, path)}>Delete</Button> */}
                        </div>
                      ))}
                    </>
                  ) :
                   <>{
                    row.paymentUploadedFiles.map(({ fileData, fileName }, i) => (
                      <div key={i} style={{ marginBottom: '10px' }}>
                        <img
                          src={fileData}
                          alt="Uploaded"
                          style={{ width: '50px', height: '50px', marginLeft: '10px', cursor: 'pointer' }}
                          onClick={() => handleImageClick(row, i)}
                        />
                        &nbsp;&nbsp;
                      <a href="#" title="Download Image" onClick={() => {                    
                        fileName = `Payment_Image_${row.id}_${i + 1}.jpg`;
                        handleDownload(fileData, fileName)
                      }}>
                        <i class="fa fa-download fa-lg"></i>
                      </a>

                      </div>
                      ))
                    } 
                  </>
                }
                </TableCell>
                <TableCell>
                  {editIndex === rowIndex ? (
                    <Select
                      name="status"
                      value={row.status}
                      onChange={event => handleInputChange(rowIndex, event)}
                    >
                      <MenuItem value="Pending">Pending</MenuItem>
                      <MenuItem value="In-Progress">In-Progress</MenuItem>
                      <MenuItem value="Completed">Completed</MenuItem>
                    </Select>
                  ) : (
                    row.status
                  )}
                </TableCell>
                <TableCell>
                  {editIndex === rowIndex ? (
                    <Button variant="contained" color="primary" onClick={() => handleSaveClick(editIndex)}>Save</Button>
                  ) : (
                    <>
                      <i className="fas fa-edit fa-lg" title="Edit" onClick={() => handleEditClick(rowIndex)}></i>&nbsp;&nbsp;&nbsp;
                      <i className="fas fa-trash fa-lg" title="Delete Row" onClick={() => removeRow(rowIndex)}></i>
                    </>
                  )}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <Dialog
        open={Boolean(selectedRow)}
        onClose={handleCloseDialog}
        maxWidth="md"
        fullWidth
      >
        <DialogTitle>
          Payment Image Preview
          <IconButton
            aria-label="close"
            onClick={handleCloseDialog}
            style={{ position: 'absolute', right: 8, top: 8 }}
          >
            <CloseIcon />
          </IconButton>
        </DialogTitle>
        <DialogContent>
          {selectedRow && selectedImageIndex !== null && (
            <>
              <TableContainer>
                <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>ID</TableCell>
                    <TableCell>Nett Payout</TableCell>
                    <TableCell>Compound</TableCell>
                    <TableCell>Balance</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                    <TableRow>
                    <TableCell>{selectedRow.id}</TableCell>
                    <TableCell>{formatterIndCurr.format(selectedRow.payout)}</TableCell>
                    <TableCell>{formatterIndCurr.format(selectedRow.compound)}</TableCell>
                    <TableCell>{formatterIndCurr.format(selectedRow.balance)}</TableCell>
                    </TableRow>                  
                  </TableBody>
                </Table>
              </TableContainer>              

              <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <Button variant="contained" onClick={handlePrevImage}>Previous</Button>
                
                <p style={{ textAlign: 'center' }}>
                {selectedImageIndex + 1} of {selectedRow.paymentUploadedFiles.length}
                </p>                               

                <a href="#" title="Download Image" onClick={() => {                    
                    const fileData =selectedRow.paymentUploadedFiles[selectedImageIndex].fileData;
                    const fileName = `Payment_Image_${selectedRow.id}_${selectedImageIndex + 1}.jpg`;
                    handleDownload(fileData, fileName)
                  }}>
                  <i class="fa fa-download fa-lg"></i>
                </a>

                <Button variant="contained" onClick={handleNextImage}>Next</Button>
              </div>
              <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>                             
                <img
                  src={selectedRow.paymentUploadedFiles[selectedImageIndex].fileData}
                  alt="Full Size"
                  style={{ width: '100%', height: 'auto' }}
                />                
              </div>
              <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <Button variant="contained" onClick={handlePrevImage}>Previous</Button>
                  
                <a href="#" title="Download Image" onClick={() => {                    
                    const fileData =selectedRow.paymentUploadedFiles[selectedImageIndex].fileData;
                    const fileName = `Payment_Image_${selectedRow.id}_${selectedImageIndex + 1}.jpg`;
                    handleDownload(fileData, fileName)
                  }}>
                  <i class="fa fa-download fa-lg"></i>
                </a>

                <Button variant="contained" onClick={handleNextImage}>Next</Button>
              </div>
              <p style={{ textAlign: 'center' }}>
                {selectedImageIndex + 1} of {selectedRow.paymentUploadedFiles.length}
              </p>
            </>
          )}
        </DialogContent>
      </Dialog>

      <LoadingDialog open={loading} onClose={() => setLoading(false)} title="Updating" message="Please wait..." />
      <ConfirmationDialog		
        open={confirmationDialogOpen}
        onClose={()=> setConfirmationDialogOpen(false)}
        onConfirm={()=> handleConfirmRemoveAllClick()}
        title="Confirm"
        message="This will delete all rows. Are you sure you want to proceed?"
      />
    </div>
  );
}

function roundUpToNearestTenThousand(value) {
  return Math.ceil(value / 10000) * 10000;
}

export default App;
